import React, { PropTypes } from 'react';
import { Title4Top, Input, Center, Button3, ErrorText } from '../styledComponents';

class PromoCodeField extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    const { promoCode, errorPromoCode, checkPromoCode } = this.props;
    return (
      <div>
        <Title4Top>Carte cadeau/code promo</Title4Top>
        <Center>
          <Input
            type="text" placeholder="code" value={this.state.promoCode}
            onChange={e => this.setState({ promoCode: e.target.value })}
          />
          <Button3
            small noMargin onClick={() => checkPromoCode(this.state.promoCode)}
            isNotValid={!this.state.promoCode}
          >Appliquer</Button3>
        </Center>
        {promoCode && promoCode.code &&
          <Center>
            {promoCode.value ?
              `${promoCode.name} vous offre ${promoCode.value}${promoCode.unit === 'EUROS' ? '€' : '%'}
              sur cette commande.`
              : `Le code ${promoCode.name} a bien été pris en compte pour cette commande.`
            }
          </Center>
        }
        {errorPromoCode &&
          <ErrorText>Code non reconnu</ErrorText>
        }
      </div>
    );
  }
}


PromoCodeField.propTypes = {
  promoCode: PropTypes.shape({}),
  checkPromoCode: PropTypes.func.isRequired,
  errorPromoCode: PropTypes.bool,
};

PromoCodeField.defaultProps = {
  promoCode: null,
  errorPromoCode: false,
};

export default PromoCodeField;



